export default PolicyBlock;

const Article = styled.article`
  display: flex;
  flex-direction: column;
  padding: 35px 22px 70px;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
`;
const Header = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #bfbfbf;

  img {
    display: block;
    width: 60px;
  }
`;
const Section = styled.section`
  width: 100%;
  border: 1px solid #ededed;
  margin-bottom: 20px;

  h3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    font-size: 15px;
    font-weight: bold;
    padding: 19px 15px;
    transition: all 0.3s;
    border-bottom: ${(props) => (props.slideDown ? "1px solid #F5F5F5" : "0")};

    i {
      display: block;
      font-size: 18px;
      transition: all 0.3s;
      transform: rotate(${(props) => (props.slideDown ? "0deg" : "180deg")});
    }
  }
  p {
    width: 100%;
    max-height: 200px;
    font-size: 12px;
    color: #8f8f8f;
    line-height: 1.5;
    padding: 19px 15px;
    transition: all 0.3s;
    overflow-y: scroll;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    ::-webkit-scrollbar {
      display: none; /* Chrome, Safari, Opera*/
    }

    strong {
      font-weight: bold;
      color: #151515;
    }

    ${(props) =>
      !props.slideDown &&
      css`
        max-height: 0;
        padding: 0px 15px;
        overflow: hidden;
      `}
  }
`;
